@import url("https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@300;400;500;600;700&family=Poppins:wght@400;500;700;900&display=swap");
:root {
  --color-gray: #333;
  --color-gray-dark: #222;
  --color-gray-light: #ccc;
  --color-gray-mediun: #666;
  --color-white: #fff;
  --color-light: #e1d9f1;
  --color-background: rgba(92, 125, 238, 0.2);
  --color-section: rgba(92, 125, 238, 0.1);
  --color-blue: #5d5fef;
  --color-green: #71c3a8;
}

* {
  padding: 0;
  margin: 0;
}

body {
  margin: 0;
  font-family: "Poppins", sans-serif;
  color: var(--color-gray-dark);
}

ul,
li {
  list-style: none;
}
